.Header {
  display: flex;
  z-index: 2;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  border-bottom: solid 2px rgba(128, 128, 128, 0.311);
  padding: 0 5vw;
  position: sticky;
  top: 0;
  text-align: center;
  background-color: rgba(198, 192, 192, 0.89);
  box-shadow: 0 3px 4px 1px rgba(0, 0, 0, 0.512);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}

.Header > h1 {
  cursor: default;
  transition: color 0.5s;
}

.cart {
  position: relative;
}

.num__items {
  position: absolute;
  bottom: 0;
  right: 3px;
  color: rgba(128, 128, 128, 0.677);
  font-size: 0.95rem;
}

@media (hover: hover) {
  .Header > h1:hover {
    color: rgb(238, 161, 161);
  }
}
